	module.exports = {"data":{"markdownRemark":{"html":"<p><a href=\"https://github.com/troygoode/node-cors\">https://github.com/troygoode/node-cors</a>\n<a href=\"https://github.com/analog-nico/hpp\">https://github.com/analog-nico/hpp</a>\n<a href=\"https://github.com/ericmdantas/express-content-length-validator\">https://github.com/ericmdantas/express-content-length-validator</a>\n<a href=\"https://github.com/aredo/express-enforces-ssl\">https://github.com/aredo/express-enforces-ssl</a></p>","frontmatter":{"date":"January 01, 1970","path":"/securing-your-expressjs-app","tags":null,"title":"securing your Expressjs app"}}},"pathContext":{"prev":{"excerpt":"Great, so finished your newly designed REST API that will back up your upcoming platform. You are about to hit live and you are expecting a fair amount of visitors, but there is the possibility of overcoming that amount and you want to test your API...","html":"<p>Great, so finished your newly designed REST API that will back up your upcoming platform. You are about to hit live and you are expecting a fair amount of visitors, but there is the possibility of overcoming that amount and you want to test your API to see how far it can go.</p>\n<p>Enter stress tests. Stress tests can simulate users that come and hit requests over your API. The frequency of those requests is the key metric here and what you really want to know is how much you can afford.</p>\n<p>The most well known stress test utility out there is Apache's AB. If you have installed Apache already you also have installed AB. So fire up your console, write <code>ab</code> and hit Enter.</p>\n<p>AB is benchmarking tool for Apache servers, but you can use it with every HTTP serving platforms, such as Nodejs apps.</p>","id":"/Users/Bariotis/Dev/develop/gatsby-blog-starter-kit/src/pages/rest-api-stress-test/index.md absPath of file >>> MarkdownRemark","timeToRead":1,"frontmatter":{"date":"1970-01-01T00:00:00.000Z","path":"/rest-api-stress-test","tags":null,"title":"REST API Stress Test"}},"next":{"excerpt":"Knowing how to communicate effectively\nvalid arguments\npoints\nknow what your talkign about\ndo your homework/research\nwant to go deeper? fallacies  http://www.nobeliefs.com/fallacies.htm Not everything is about you. Everything is about the team...","html":"<ol>\n<li>\n<p>Knowing how to communicate effectively\nvalid arguments\npoints\nknow what your talkign about\ndo your homework/research\nwant to go deeper? fallacies <a href=\"http://www.nobeliefs.com/fallacies.htm\">http://www.nobeliefs.com/fallacies.htm</a></p>\n</li>\n<li>\n<p>Not everything is about you. Everything is about the team. Disagreeing will only slow the whole team down.\nKnow when to hold back. When to say, you know what, I do disagree with all of you, but let's try that and see what happens.</p>\n</li>\n</ol>","id":"/Users/Bariotis/Dev/develop/gatsby-blog-starter-kit/src/pages/tell-me-about-a-situation-where-you-were-disagreeing-with-other-people-on-the-team/index.md absPath of file >>> MarkdownRemark","timeToRead":1,"frontmatter":{"date":"1970-01-01T00:00:00.000Z","path":"/tell-me-about-a-situation-where-you-were-disagreeing-with-other-people-on-the-team","tags":null,"title":"Tell me about a situation where you were disagreeing with other people on the team"}}}}

